import selfStartTask from '@/components/self-start-task.vue';
import selfInput from '@/components/self-input.vue';
import selfButton from '@/components/self-button.vue';
import timer from '@/components/timer/index.vue';

export default {
	data() {
		return {
			detail: {},
			taskkind: [ "A", "B", "C" ],
			taskkindidx: 0,
			pigerAds: [ "A", "B", "C" ],
			pigerAdsidx: 0,
			plainTime: '',
			address: ['猪场A', '猪场B', '猪场C'],
			idx: 0,
			accompanying: [],
			accShow: false,
			othserUser: '',
			othserSfz: '',
			drivers: ["A司机", "B司机", "C司机"],
			driversidx: 0,
			pigerIn: ['1号进猪口', '2号进猪口', '3号进猪口'],
			pigerInidx: 0,
			carIdno: ''
		}
	},
	components: {
		selfStartTask,
		selfInput,
		timer,
		selfButton
	},
	onLoad(options) {
		const { detail } = options;
		if(detail) {
			this.detail = JSON.parse(detail);
			console.log('-----', this.detail)
		}
	},
	methods: {
		taskkindChange(e) {
			this.taskkindidx = e.target.value
		},
		pigerAdsChange(e) {
			this.pigerAdsidx = e.target.value
		},
		driversChange(e) {
			this.driversidx = e.target.value
		},
		pigerInChange() {
			this.pigerInidx = e.target.value
		},
		plainTimeChange(e) {
			this.plainTime = e
		},
		addressChange(e) {
			this.idx = e.target.value
		},
		addUserHandle() {
			this.accShow = true
		},
		addHandle() {
			if(!this.carIdno) {
				uni.showToast({ title: '请输入车牌号', icon: 'none' });
				return
			}
			this.accompanying.push({carIdno: this.carIdno, driver: this.drivers[this.driversidx], piger: this.pigerIn[this.pigerInidx]})
			this.carIdno = '';
			this.driversidx = 0;
			this.pigerInidx = 0;
			this.accShow = false
		},
		cancleHandle() {
			this.carIdno = '';
			this.driversidx = 0;
			this.pigerInidx = 0;
			this.accShow = false
		},
		deleteHandle(e, index) {
			this.accompanying.splice(index, 1);
		},
		okClick() {
			alert('a')
		}
	}
}
